body {
  margin: 0;
  padding: 0;
  max-width: 100vw;
  max-height: 100vh;
  overflow: hidden;
  background: linear-gradient(90deg, #fa0909, #2f04da);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  padding: 15px;
  margin: 30px auto;
  max-width: 60%;
  overflow-x: hidden;
  background-color: whitesmoke;
  border-radius: 50px;
}

.container {
  padding: 20px;
}
.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

label,
input {
  margin-top: 10px;
  padding: 5px 10px;
}

.btnAddTask {
  margin-top: 20px;
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 10px 15px;
  text-transform: uppercase;
  border-radius: 5px;
  cursor: pointer;
}

.btnAddTask:hover {
  background-color: #3f9142;
}

.tasksActive,
.tasksDone {
  padding: 20px;
}

.emptyTasks {
  color: red;
  font-weight: 600;
}

.task {
  font-style: italic;
}

.taskBtn {
  margin-left: 5px;
  border: 1px solid black;
  cursor: pointer;
  color: white;
  font-weight: 800;
  border: none;
}

.taskBtnCheck {
  background-color: #4caf50;
}

.taskBtnDelete {
  background-color: red;
}

